<template>
  <section>
    <transition-group name="list" tag="ul">
      <li
        v-for="board in boardlist"
        :key="board.no"
        @click="showDetailBoard(board.no)"
        class="shadow"
      >
        <span> {{ board.no }}. {{ board.content }} {{ board.writer }} </span>
      </li>
    </transition-group>
    <BoardFooter></BoardFooter>
  </section>
</template>

<script>
import BoardFooter from "./BoardFooter.vue";
import { mapGetters } from "vuex";
export default {
  components: {
    BoardFooter,
  },
  created() {
    this.$store.dispatch("ALLBOARD");
  },
  computed: {
    ...mapGetters(["boardlist"]),
  },
  methods: {
    showDetailBoard(no) {
      this.$router.push("/detail/" + no);
    },
  },
};
</script>

<style scoped>
.del {
  text-decoration: line-through;
}
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}
li {
  display: flex;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 5px;
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
</style>
