<template>
  <div class="inputBox shadow">
    <span>
      제목
      <input type="text" v-model="title" />
      <br />
    </span>
    <span>
      작성자
      <input type="text" v-model="writer" />
      <br />
    </span>
    <span>
      내용
      <input type="text" v-model="content" />
      <br />
    </span>
    <button @click="addTodo">등록</button>
    &nbsp;
    <button @click="cancle">돌아가기</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: null,
      content: null,
      writer: null,
    };
  },
  methods: {
    addTodo() {
      if (this.title != null && this.content != null && this.writer != null) {
        const newBoard = {
          title: this.title,
          content: this.content,
          writer: this.writer,
        };
        this.$store.dispatch("ADDBOARD", newBoard);
        this.$router.push("/");
      } else {
        alert("내용을 입력해 주세요");
      }
    },

    cancle() {
      this.$router.push("/");
    },
  },
};
</script>

<style>
input {
  outline: none;
  background: gainsboro;
}
.inputBox {
  background: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.inputBox input {
  border-style: none;
  font-size: 0.9rem;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478fb, #8763fb);
  display: inline-block;
  width: 3rem;
  border-radius: 0 5px 5px 0;
}
.addBtn {
  color: white;
  vertical-align: middle;
}
</style>
