import Vue from "vue";
import VueRouter from "vue-router";

import BoardList from "../components/BoardList.vue";
import BoardRegister from "../components/BoardRegister.vue";
import BoardDetail from "../components/BoardDetail.vue";

Vue.use(VueRouter);
//플러그인은 여러 컴포넌트에서 사용되는(특히 전역) 기능을 만들 때 사용
//VueRouter(플러그인)를 전역수준의 기능으로 Vue 객체에 추가해 줌
//여러 컴포넌트에서 this.$router로 접근 가능

export default new VueRouter({
  mode: "history",
  routes: [
    { path: "/", component: BoardList },
    { path: "/list", component: BoardList },
    { path: "/register", component: BoardRegister },
    { path: "/detail/:no", component: BoardDetail },
  ],
});
